.filter-section {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: 30px 0;
}

.filter-section .btn {
    background-color: #343a40 !important;
    border-color: #343a40 !important;
}

.spots-list {
    height: 400px;
    overflow-y: auto;
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
}

.spots-list-header {
    background-color: rgba(0, 0, 0, 0.03) !important;
}

.spots-list-header > h4 {
    margin-bottom: 0;
}

.spots-list p {
    margin: 0;
}

.spots-section {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    text-align: center;
}

.autocomplete-dropdown-container {
    position: absolute;
    z-index: 10;
    max-width: 600px;
}

@media screen and (max-width: 768px) {
    .filter-section {
        flex-direction: column;
        align-items: inherit;
    }

    .filter-section > div {
        margin-bottom: 20px;
    }

    .spots-list,
    .spots-section .card {
        margin-bottom: 30px;
    }
}

@media screen and (min-width: 768px) {
    .filter-section > .form-group {
        margin-bottom: 0;
    }
}
